import { redirect } from "next/navigation";
import { createClient } from "@/lib/supabase/server";
import { CreatePost } from "@/components/post/create-post";
import { Feed } from "@/components/feed/feed";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { AlertCircle } from "lucide-react";

export default async function HomePage(): Promise<React.JSX.Element> {
  const supabase = await createClient();

  // Provjeravamo da li je korisnik logovan
  const {
    data: { user },
  } = await supabase.auth.getUser();

  // Ako korisnik nije logovan, dozvoljavamo pristup ali bez mogućnosti kreiranja posta
  const isAuthenticated = !!user;

  // Dobavljamo profil ako je logovan
  const { data: profile, error: profileError } = isAuthenticated 
    ? await supabase.from("profiles").select("*").eq("id", user.id).maybeSingle()
    : { data: null, error: null };

  // Provjera da li tabela postoji
  const databaseNotSetup = profileError?.message?.includes("Could not find the table");

  if (databaseNotSetup) {
    return (
      <div className="min-h-screen bg-background">
        <div className="mx-auto max-w-2xl p-4">
          <div className="sticky top-0 z-10 border-b bg-card/80 backdrop-blur-sm">
            <div className="p-4">
              <h1 className="text-xl font-bold">Home</h1>
            </div>
          </div>

          <div className="p-4">
            <Alert>
              <AlertCircle className="h-4 w-4" />
              <AlertTitle>Database Setup Required</AlertTitle>
              <AlertDescription>
                The database tables haven't been created yet. Please run the SQL scripts in the{" "}
                <code className="rounded bg-muted px-1 py-0.5">scripts</code> folder to set up your database:
                <ul className="mt-2 list-inside list-disc space-y-1">
                  <li>01-create-tables.sql</li>
                  <li>02-enable-rls.sql</li>
                  <li>03-create-functions.sql</li>
                </ul>
              </AlertDescription>
            </Alert>
          </div>
        </div>
      </div>
    );
  }

  // Ako je logovan ali nema profil
  if (isAuthenticated && !profile && !profileError && !databaseNotSetup) {
    return (
      <div className="min-h-screen bg-background">
        <div className="mx-auto max-w-2xl p-4">
          <div className="sticky top-0 z-10 border-b bg-card/80 backdrop-blur-sm">
            <div className="p-4">
              <h1 className="text-xl font-bold">Home</h1>
            </div>
          </div>

          <div className="p-4">
            <Alert>
              <AlertCircle className="h-4 w-4" />
              <AlertTitle>Profile Setup Required</AlertTitle>
              <AlertDescription>
                Your profile hasn't been created yet. This might happen if you signed up before the database trigger was
                set up. Please sign out and sign in again, or contact support.
              </AlertDescription>
            </Alert>
          </div>
        </div>
      </div>
    );
  }

  // Loading state za logovane korisnike
  if (isAuthenticated && !profile) {
    return (
      <div className="min-h-screen bg-background">
        <div className="mx-auto max-w-2xl p-4">
          <div className="p-8 text-center text-muted-foreground">
            <p>Loading your profile...</p>
          </div>
        </div>
      </div>
    );
  }

  // Dohvatimo korisnike koje prati trenutni korisnik (ako je logovan)
  let followingUserIds: string[] = [];
  
  if (isAuthenticated && profile) {
    const { data: follows } = await supabase
      .from("follows")
      .select("following_id")
      .eq("follower_id", profile.id);
    
    followingUserIds = follows?.map(follow => follow.following_id) || [];
    
    // Uključujemo i sopstveni ID da bi videli i svoje postove
    followingUserIds.push(profile.id);
  }

  // Pripremimo parametre za Feed komponentu
  const feedParams = {
    userId: isAuthenticated ? profile?.id : undefined,
    followingUserIds: followingUserIds.length > 0 ? followingUserIds : undefined,
    isAuthenticated,
  };

  return (
    <div className="min-h-screen bg-background">
      <div className="mx-auto max-w-2xl">
        <div className="sticky top-0 z-10 border-b bg-card/80 backdrop-blur-sm">
          <div className="p-4">
            <h1 className="text-xl font-bold">
              {isAuthenticated ? "Home" : "Public Feed"}
            </h1>
            {!isAuthenticated && (
              <p className="text-sm text-muted-foreground mt-1">
                Sign in to create posts and follow users
              </p>
            )}
          </div>
        </div>

        {isAuthenticated && profile && (
          <CreatePost
            user={{
              id: profile.id,
              username: profile.username,
              display_name: profile.display_name,
              avatar_url: profile.avatar_url,
            }}
          />
        )}

        <Feed {...feedParams} />
      </div>
    </div>
  );
}